@keyframes show{0%{transform:scale(1);opacity:0}90%{transform:scale(1.5);opacity:0.8}100%{transform:scale(1.5);opacity:0}}.infobox{flex:1;display:flex;align-items:center;justify-content:center}.infobox :global(p){font-size:40px;color:#f8f8f8;opacity:0;transition:none}.infobox :global(p.showing){animation-name:show;animation-duration:.5s}



